import React, { Component } from "react";
import Logo from "../../domestic-logo.png";

import { Navbar, Nav, NavDropdown } from "react-bootstrap";
export default class CustomNavbar extends Component {
  render() {
    return (
      <Navbar collapseOnSelect expand="lg" bg="dark" variant="dark">
        <Navbar.Brand href="#home">
          <img
            style={{
              width: "60px"
            }}
            src={Logo}
          ></img>
        </Navbar.Brand>
        <Navbar.Toggle aria-controls="responsive-navbar-nav" />
        <Navbar.Collapse id="responsive-navbar-nav">
          <Nav className="mr-auto">
            <h1>Domestic Solutions</h1>
            <Nav.Link href="#features">About</Nav.Link>
            <Nav.Link href="#pricing">FAQs</Nav.Link>
            <NavDropdown title="Services" id="collasible-nav-dropdown">
              <NavDropdown.Item href="#action/3.1">Plumber</NavDropdown.Item>
              <NavDropdown.Item href="#action/3.1">
                Electrician
              </NavDropdown.Item>
              <NavDropdown.Item href="#action/3.1">Carpenter</NavDropdown.Item>
              <NavDropdown.Item href="#action/3.1">Painter</NavDropdown.Item>
              <NavDropdown.Item href="#action/3.1">
                AC/Fridge Repair
              </NavDropdown.Item>
            </NavDropdown>
          </Nav>
          <Nav>
            <Nav.Link href="/register">Sign Up</Nav.Link>
            <Nav.Link href="/login">Login</Nav.Link>
          </Nav>
        </Navbar.Collapse>
      </Navbar>
    );
  }
}
