import React, { Component } from "react";
import {
  Navbar,
  Container,
  Row,
  Col,
  Card,
  Form,
  Button,
  Nav,
  Tab
} from "react-bootstrap";
import CustomNavbar from "../Navbar/Navbar";
import app from "../../firebase";

export default class Register extends Component {
  state = {
    image: null,
    Name: "",
    CNIC: "",
    Email: "",
    Password: "",
    CellNo: "",
    Address: "",
    Shopreference: "",
    Expertise: "",
    City: "",
    State: ""
  };
  regiter = async () => {
    const user = await app
      .auth()
      .createUserWithEmailAndPassword(this.state.Email, this.state.Password);
    await app
      .database()
      .ref("/customers/" + user.user.uid)
      .set({
        Name: this.state.Name,
        CNIC: this.state.CNIC,
        Email: this.state.Email,
        CellNo: this.state.CellNo,
        Address: this.state.Address,
        City: this.state.City,
        State: this.state.State
      });
  };
  fileInputHandler = e => {
    console.log(e.target.files[0]);
    this.setState({
      [e.target.name]: e.target.files[0]
    });
  };
  render() {
    return (
      <div>
        <CustomNavbar></CustomNavbar>
        <Container>
          <Row>
            <Col>
              <Card body className="mt-5">
                <Tab.Container defaultActiveKey="customer">
                  <Nav variant="pills" fill>
                    <Nav.Item>
                      <Nav.Link eventKey="customer">Customer</Nav.Link>
                    </Nav.Item>
                    <Nav.Item>
                      <Nav.Link eventKey="service">Service Provider</Nav.Link>
                    </Nav.Item>
                  </Nav>

                  <Tab.Content style={{ marginTop: 20 }}>
                    <Tab.Pane eventKey="customer">
                      <Form>
                        <Form.Group controlId="formGridName">
                          <Form.Label>Name</Form.Label>
                          <Form.Control
                            onChange={e => {
                              this.setState({
                                Name: e.target.value
                              });
                            }}
                            type="text"
                            placeholder="Enter Name"
                          />
                        </Form.Group>
                        <Form.Group controlId="formGridCNIC">
                          <Form.Label>CNIC</Form.Label>
                          <Form.Control
                            onChange={e => {
                              this.setState({
                                CNIC: e.target.value
                              });
                            }}
                            type="text"
                            placeholder="Enter CNIC"
                          />
                        </Form.Group>
                        <Form.Row>
                          <Form.Group as={Col} controlId="formGridEmail">
                            <Form.Label>Email</Form.Label>
                            <Form.Control
                              onChange={e => {
                                this.setState({
                                  Email: e.target.value
                                });
                              }}
                              type="email"
                              placeholder="Enter email"
                            />
                          </Form.Group>

                          <Form.Group as={Col} controlId="formGridPassword">
                            <Form.Label>Password</Form.Label>
                            <Form.Control
                              onChange={e => {
                                this.setState({
                                  Password: e.target.value
                                });
                              }}
                              type="password"
                              placeholder="Password"
                            />
                          </Form.Group>
                        </Form.Row>
                        <Form.Group controlId="formGridCellNo">
                          <Form.Label>Cell No</Form.Label>
                          <Form.Control
                            onChange={e => {
                              this.setState({
                                CellNo: e.target.value
                              });
                            }}
                            type="text"
                            placeholder="Enter Cell No"
                          />
                        </Form.Group>

                        <Form.Group controlId="formGridAddress">
                          <Form.Label>Address</Form.Label>
                          <Form.Control
                            onChange={e => {
                              this.setState({
                                Address: e.target.value
                              });
                            }}
                            placeholder="1234 Main St"
                          />
                        </Form.Group>

                        <Form.Row>
                          <Form.Group as={Col} controlId="formGridCity">
                            <Form.Label>City</Form.Label>
                            <Form.Control
                              onChange={e => {
                                this.setState({
                                  City: e.target.value
                                });
                              }}
                            />
                          </Form.Group>

                          <Form.Group as={Col} controlId="formGridState">
                            <Form.Label>State</Form.Label>
                            <Form.Control
                              onChange={e => {
                                this.setState({
                                  State: e.target.value
                                });
                              }}
                              as="select"
                            >
                              <option>Choose...</option>
                              <option>...</option>
                            </Form.Control>
                          </Form.Group>
                        </Form.Row>

                        <Button
                          variant="primary"
                          onClick={this.regiter}
                          type="button"
                        >
                          Register as a Customer
                        </Button>
                      </Form>
                    </Tab.Pane>
                    <Tab.Pane eventKey="service">
                      <Form>
                        <div className="d-flex flex-column justify-content-center align-items-center">
                          <img
                            style={{
                              width: "200px",
                              height: "200px",
                              objectFit: "cover",
                              borderRadius: "50%"
                            }}
                            src={
                              this.state.image
                                ? URL.createObjectURL(this.state.image)
                                : "https://img.pngio.com/profile-icon-png-image-free-download-searchpngcom-profile-icon-png-673_673.png"
                            }
                          ></img>
                          <Form.Group
                            className="mt-5"
                            controlId="formGridAddress1"
                          >
                            <Form.Control
                              className="btn btn-primary"
                              onChange={this.fileInputHandler}
                              name="image"
                              accept="image/*"
                              type="file"
                            />
                          </Form.Group>
                        </div>
                        <Form.Group controlId="formGridName">
                          <Form.Label>Name</Form.Label>
                          <Form.Control type="text" placeholder="Enter Name" />
                        </Form.Group>
                        <Form.Group controlId="formGridCNIC">
                          <Form.Label>CNIC</Form.Label>
                          <Form.Control type="text" placeholder="Enter CNIC" />
                        </Form.Group>
                        <Form.Row>
                          <Form.Group as={Col} controlId="formGridEmail">
                            <Form.Label>Email</Form.Label>
                            <Form.Control
                              type="email"
                              placeholder="Enter email"
                            />
                          </Form.Group>

                          <Form.Group as={Col} controlId="formGridPassword">
                            <Form.Label>Password</Form.Label>
                            <Form.Control
                              type="password"
                              placeholder="Password"
                            />
                          </Form.Group>
                        </Form.Row>
                        <Form.Group controlId="formGridCellNo">
                          <Form.Label>Cell No</Form.Label>
                          <Form.Control
                            type="text"
                            placeholder="Enter Cell No"
                          />
                        </Form.Group>

                        <Form.Group controlId="formGridAddress">
                          <Form.Label>Address</Form.Label>
                          <Form.Control placeholder="1234 Main St" />
                        </Form.Group>

                        <Form.Group controlId="formGridShopreference">
                          <Form.Label>Shop Reference</Form.Label>
                          <Form.Control
                            type="text"
                            placeholder="Enter Shop Reference"
                          />
                        </Form.Group>

                        <Form.Group controlId="formGridExpertise">
                          <Form.Label>Expertise</Form.Label>
                          <Form.Control as="select">
                            <option>Choose...</option>
                            <option>...</option>
                          </Form.Control>
                        </Form.Group>

                        <Form.Row>
                          <Form.Group as={Col} controlId="formGridCity">
                            <Form.Label>City</Form.Label>
                            <Form.Control />
                          </Form.Group>

                          <Form.Group as={Col} controlId="formGridState">
                            <Form.Label>State</Form.Label>
                            <Form.Control as="select">
                              <option>Choose...</option>
                              <option>...</option>
                            </Form.Control>
                          </Form.Group>
                        </Form.Row>

                        <Button variant="primary" type="submit">
                          Register as Service Provider
                        </Button>
                      </Form>
                    </Tab.Pane>
                  </Tab.Content>
                </Tab.Container>
              </Card>
            </Col>
          </Row>
        </Container>
      </div>
    );
  }
}
